import { useSelector, useDispatch } from "react-redux";
import { styled, useTheme } from "@mui/material/styles";
import ReactApexChart from "react-apexcharts";
import { useState, useEffect, useRef } from "react";
import Box from "@mui/material/Box";
import { selectContrastMainTheme } from "app/store/fuse/settingsSlice";
import Paper from "@mui/material/Paper";
import { selectWidgets, setExportedData, setExportedSummaryInfo, getWidgets } from "../store/widgetsSlice";
import Select from "@mui/material/Select";
import MenuItem from "@mui/material/MenuItem";
import InputLabel from "@mui/material/InputLabel";
import SummaryInfo from "../../components/SummaryInfo";
import moment from "moment-timezone";
import { format } from "date-fns";
import { es } from "date-fns/locale";

const Root = styled(Paper)(({ theme }) => ({
  //background: theme.palette.primary.main,
  color: theme.palette.primary.contrastText,
}));

function VisitorsOverviewWidget() {
  const dispatch = useDispatch();

  const theme = useTheme();
  const contrastTheme = useSelector(selectContrastMainTheme(theme.palette.primary.main));
  const widgets = useSelector(selectWidgets);
  //const { data } = widgets.widgets?.users ?? { data: [], ranges: {} };
  const { SummaryInfoData } = widgets?.SummaryInfoData ?? {};
  const [series, setSeries] = useState([]);
  const [chartReady, setChartReady] = useState(false);
  const [selectedYear, setSelectedYear] = useState(new Date().getFullYear().toString()); //

  const [selectedMonth, setSelectedMonth] = useState("Todos"); // Para almacenar el mes seleccionado
  const [data, setData] = useState([]);
  const monthsOfYearRef = useRef([]);
  const monthsOfYear = monthsOfYearRef.current;
  const currentYear = new Date().getFullYear();
  const previousYear = currentYear - 1; // Año anterior

  // Opciones del select para el año actual y el año anterior
  const yearOptions = [
    { value: currentYear, label: currentYear.toString() },
    { value: previousYear, label: previousYear.toString() },
  ];

  const [selectedMonthDays, setSelectedMonthDays] = useState([]);

  const chartOptions = {
    chart: {
      height: 300,
      type: "area",
      toolbar: {
        show: true,
        tools: {
          download: true,
          selection: false,
          zoom: false,
          zoomin: false,
          zoomout: false,
          pan: false,
          reset: false,
        },
        export: {
          csv: {
            filename: selectedMonth !== "Todos" ? `Filtro: ${selectedYear} - ${selectedMonth}` : "Todos los registros",
          },
        },
      },
    },

    dataLabels: {
      enabled: false,
    },
    stroke: {
      curve: "smooth",
    },

    xaxis: {
      categories:
        selectedMonth === "Todos" ? ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"] : selectedMonthDays,
    },
    title: {
      text: "Ventas",
      align: "left",
      margin: 30,
      offsetY: -10,
      style: {
        fontSize: "18px",
        color: "black",
        fontWeight: "normal",
      },
    },
    subtitle: {
      text: selectedMonth !== "Todos" ? `Filtro: ${selectedYear} - ${selectedMonth}` : "",
      align: "left",
      style: {
        fontSize: "14px",
        color: "gray",
        fontWeight: "normal",
      },
    },
  };

  const monthNames = ["Enero", "Febrero", "Marzo", "Abril", "Mayo", "Junio", "Julio", "Agosto", "Septiembre", "Octubre", "Noviembre", "Diciembre"];

  //genera los meses en el select
  const generateMonths = (selectedYear) => {
    const months = [];
    if (selectedYear == currentYear) {
      // Para el año actual, muestra solo los meses hasta el mes actual
      const currentMonth = new Date().getMonth();
      for (let i = 0; i <= currentMonth; i++) {
        months.push(monthNames[i]);
      }
    } else {
      // Para otros años, muestra todos los meses (12 meses)
      for (let i = 0; i <= 11; i++) {
        months.push(monthNames[i]);
      }
    }
    monthsOfYearRef.current = ["Todos", ...months];
  };

  const [summaryInfoData, setSummaryInfoData] = useState({
    total: 333333333,
    clientes_compra: 135123,
    efectividad: 49.75,
    cobertura: 22.33,
    compra_promedio: {
      drop: 11,
      ref: 23,
      frec: 3,
    },
  });

  useEffect(() => {
    generateMonths(selectedYear);
  }, []);

  useEffect(() => {
    dispatch(getWidgets()).then((resp) => {
      dispatch(setExportedData(resp.payload.users.data));
      setData(resp.payload.users.data);
    });
  }, [dispatch]);

  // Define una función para manejar la actualización del estado con el valor filtrado
  const updateStateWithFilteredData = (filteredData) => {
    // Despachar setExportedData con el valor filtrado
    dispatch(setExportedData(filteredData));
  };

  useEffect(() => {
    ///datos anuales
    const filterAndSumByMonth = (data, year) => {
      const filteredData = data.filter((item) => {
        const itemDate = moment(item.fecha).tz("America/Argentina/Buenos_Aires");
        const itemYear = itemDate.year();
        return itemYear === year;
      });
      // Generate the uniqueMonths array for the current year
      const uniqueMonths = [...new Set(filteredData.map((item) => item.fecha.slice(0, 7)))];

      const areaChartData = uniqueMonths.map((month) => {
        const usuariosPorMes = filteredData.filter((item) => item.fecha.slice(0, 7) === month).reduce((total, item) => total + item.usuarios, 0);
        return usuariosPorMes;
      });
      // Fill in missing months with 0
      const allMonths = ["01", "02", "03", "04", "05", "06", "07", "08", "09", "10", "11", "12"];
      const filledChartData = allMonths.map((month) => {
        const index = uniqueMonths.indexOf(year + "-" + month);
        return index !== -1 ? areaChartData[index] : 0;
      });

      return filledChartData;
    };

    // Obtener datos del año anterior
    const historicalData = filterAndSumByMonth(data, currentYear - 1);

    // Obtener datos del año en curso sin meses sin datos disponibles
    const currentYearData = filterAndSumByMonth(data, currentYear);

    // Obtener datos del mes  y año seleccionado
    const filterData = (data, year, month) => {
      return data.filter((item) => {
        const itemDate = moment(item.fecha).tz("America/Argentina/Buenos_Aires");
        itemDate.locale("es");
        const itemMonth = format(itemDate.toDate(), "MMMM", { locale: es });
        const itemYear = itemDate.year();
        return itemYear.toString() === year.toString() && itemMonth.toString() === month.toLowerCase().toString();
      });
    };

    // Filtra la data en base al año y el mes seleccionado
    const filtered = filterData(data, selectedYear, selectedMonth);

    if (selectedMonth === "Todos") {
      setSeries([
        { name: previousYear.toString(), data: historicalData, color: "#ffb45c" },
        { name: currentYear.toString(), data: currentYearData },
      ]);

      updateStateWithFilteredData(data);
    } else {
      // Si se selecciona un mes específico, usa los datos filtrados
      const filteredMonthData = filtered.map((item) => item.usuarios);
      setSeries([{ name: selectedMonth, data: filteredMonthData }]);

      // Llama a la función de actualización del estado con los datos filtrados
      updateStateWithFilteredData(filtered);

      const generateSelectedMonthDays = (selectedYear, selectedMonth) => {
        const filteredData = filterData(data, selectedYear, selectedMonth);
        const daysWithData = filteredData.map((item) => moment(item.fecha).tz("America/Argentina/Buenos_Aires").date());
        setSelectedMonthDays(daysWithData);
      };
      generateSelectedMonthDays(selectedYear, selectedMonth);
    }

    setChartReady(true);

    // TODO segun los datos de selectdMonth y selectedYear llamar api para obtener los datos de summaryInfo
    if (selectedMonth !== "Todos") {
      setSummaryInfoData({
        total: 12354,
        clientes_compra: 1235,
        efectividad: 22,
        cobertura: 13,
        compra_promedio: {
          drop: 3,
          ref: 2,
          frec: 1,
        },
      });
      dispatch(
        setExportedSummaryInfo({
          total: 12354,
          clientes_compra: 1235,
          efectidad: 22,
          cobertura: 13,
          compra_promedio: {
            drop: 3,
            ref: 2,
            frec: 1,
          },
        })
      );
    } else {
      setSummaryInfoData({
        total: 333333333,
        clientes_compra: 135123,
        efectividad: 49.75,
        cobertura: 22.33,
        compra_promedio: {
          drop: 11,
          ref: 23,
          frec: 3,
        },
      });
      dispatch(
        setExportedSummaryInfo({
          total: 333333333,
          clientes_compra: 135123,
          efectidad: 49.75,
          cobertura: 22.33,
          compra_promedio: {
            drop: 11,
            ref: 23,
            frec: 3,
          },
        })
      );
    }
  }, [data, SummaryInfoData, selectedMonth, selectedYear]);

  const handleYearChange = (event) => {
    setSelectedYear(event.target.value);
    setSelectedMonth("Todos");
    generateMonths(event.target.value);
  };

  return (
    <>
      <div className="flex flex-row mb-20" style={{ backgroundColor: "white", justifyContent: "left", borderRadius: "10px" }}>
        <Box sx={{ m: 1, minWidth: 150 }} size="small" className=" flex flex-row justify-end items-center">
          <InputLabel id="select-label" className="mr-10">
            Seleccionar Período:
          </InputLabel>
          <Select
            value={selectedYear} // Usa el valor seleccionado
            onChange={handleYearChange}
            color="secondary"
            size="small"
          >
            {yearOptions.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </Select>
        </Box>
        <Box sx={{ m: 1, minWidth: 150 }} size="small" className=" flex flex-row items-center">
          <Select value={selectedMonth} onChange={(event) => setSelectedMonth(event.target.value)} color="secondary" size="small">
            {monthsOfYear.map((month) => (
              <MenuItem key={month} value={month}>
                {month}
              </MenuItem>
            ))}
          </Select>
        </Box>
      </div>

      <Root className="flex flex-row flex-auto shadow rounded-2xl overflow-hidden">
        <div style={{ flex: 1 }}>
          <div className="flex flex-col flex-auto h-320 mt-20" style={{ color: "black" }}>
            {chartReady && series.length > 0 ? (
              <ReactApexChart options={chartOptions} series={series} type={chartOptions.chart.type} height={chartOptions.chart.height} />
            ) : (
              <div>Loading chart...</div>
            )}
          </div>
        </div>
        <SummaryInfo summaryInfoData={summaryInfoData} />
      </Root>
    </>
  );
}

export default VisitorsOverviewWidget;
