import { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import _ from "@lodash";
import FusePageSimple from "@fuse/core/FusePageSimple";
import { motion } from "framer-motion";
import { getWidgets, selectWidgets, selectExportedData, selectSummaryInfo } from "../store/widgetsSlice";
import VisitorsOverviewWidget from "../widgets/VisitorsOverviewWidget";
import ColumnChartUsers from "../widgets/ColumnChartUsers";
import Button from "@mui/material/Button";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import * as XLSX from "xlsx";

function GeneralTab() {
  const dispatch = useDispatch();
  const widgets = useSelector(selectWidgets);

  const exportedData = widgets.exportedData;
  const exportedSummaryInfo = widgets.exportedSummaryInfo;

  const handleExportData = () => {
    const workbook = XLSX.utils.book_new();
    const sheets = [
      {
        name: "Ventas",
        data: exportedData,
      },
      {
        name: "Resumen",
        data: [
          {
            total: exportedSummaryInfo.total,
            clientes_compra: exportedSummaryInfo.clientes_compra,
            efectidad: exportedSummaryInfo.efectidad,
            cobertura: exportedSummaryInfo.cobertura,
            compra_promedio_drop: exportedSummaryInfo.compra_promedio.drop,
            compra_promedio_ref: exportedSummaryInfo.compra_promedio.ref,
            compra_promedio_frec: exportedSummaryInfo.compra_promedio.frec,
          },
        ],
      },
    ];

    sheets.forEach((sheet) => {
      const worksheet = XLSX.utils.json_to_sheet(sheet.data);
      XLSX.utils.book_append_sheet(workbook, worksheet, sheet.name);
    });

    // Guardar el archivo
    const excelBuffer = XLSX.write(workbook, {
      bookType: "xlsx",
      type: "array",
    });
    const blob = new Blob([excelBuffer], { type: "application/octet-stream" });
    saveAs(blob, "data.xlsx");
  };

  useEffect(() => {
    dispatch(getWidgets());
  }, [dispatch]);

  return (
    <FusePageSimple
      //   header={<AnalyticsDashboardAppHeader />}
      content={
        <>
          {useMemo(() => {
            const container = {
              show: {
                transition: {
                  staggerChildren: 0.06,
                },
              },
            };

            const item = {
              hidden: { opacity: 0, y: 20 },
              show: { opacity: 1, y: 0 },
            };

            return (
              !_.isEmpty(widgets) && (
                <motion.div
                  className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-32 w-full p-24 md:p-32"
                  variants={container}
                  initial="hidden"
                  animate="show"
                >
                  <div className="sm:col-span-2 lg:col-span-4 justify-self-end">
                    <Button
                      onClick={() => handleExportData()}
                      className="whitespace-nowrap"
                      variant="contained"
                      color="secondary"
                      startIcon={<FuseSvgIcon size={20}>heroicons-solid:save</FuseSvgIcon>}
                    >
                      Exportar
                    </Button>
                  </div>

                  <motion.div variants={item} className="sm:col-span-2 lg:col-span-4">
                    <VisitorsOverviewWidget />
                  </motion.div>

                  <motion.div variants={item} className="sm:col-span-2 lg:col-span-4 ">
                    <ColumnChartUsers
                      title_y="usuarios"
                      stacked={false}
                      //     data={widgets.activeUsers.data}
                      categories={["kioscos", "almacenes", "self service", "emergentes"]}
                      title={"Usuarios activos vs nuevos "}
                    />
                  </motion.div>
                </motion.div>
              )
            );
          }, [widgets])}
        </>
      }
    />
  );
}

export default GeneralTab;
